<script lang="ts">import { onMount } from 'svelte';
import { createPoint } from '../helpers';
;
let container;
let cubes = [];
const concentricCubes = (center, min, max, count) => {
    const diff = max - min;
    const step = Math.ceil(diff / count);
    return [...new Array(count)].map((v, i) => ({
        center,
        size: step + step * i,
    }));
};
const render = () => {
    const height = container.offsetHeight;
    const width = container.offsetWidth;
    const center = createPoint((width / 3) * 2, (height / 3) * 2);
    const minSize = width * 0.035;
    const maxSize = Math.sqrt(Math.pow(width, 2) + Math.pow(height, 2));
    cubes = concentricCubes(center, minSize, maxSize, 8);
};
const deferRender = () => {
    setTimeout(() => {
        requestAnimationFrame(render);
    }, 1);
};
onMount(deferRender);
</script>

<style type="text/postcss">
  @-webkit-keyframes cubeSpin {
    0% {
      transform: rotateX(0deg) rotateY(0) rotateZ(0) translateZ(0);
    }
    100% {
      transform: rotateX(360deg) rotateY(360deg) rotateZ(0) translateZ(0);
    }
  }

  @keyframes cubeSpin {
    0% {
      transform: rotateX(0deg) rotateY(0) rotateZ(0) translateZ(0);
    }
    100% {
      transform: rotateX(360deg) rotateY(360deg) rotateZ(0) translateZ(0);
    }
  }

  .container {
    display: flex;
    flex: 1;
    width: 100%;
    height: 100%;
    position: relative;
    perspective: 800px;
    overflow: hidden;
  }

  .cube {
    --cx: 100px;
    --cy: 100px;
    --cube-size: 100px;
    --delay: 15ms;
    --speed: 30s;
    position: absolute;
    top: calc(var(--cy) - (var(--cube-size) / 2));
    left: calc(var(--cx) - (var(--cube-size) / 2));
    display: flex;
    width: var(--cube-size);
    height: var(--cube-size);
    transform-style: preserve-3d;
    -webkit-backface-visibility: visible;
            backface-visibility: visible;
    transform: rotateX(0) rotateY(0) rotateZ(0) translateZ(0);
    -webkit-animation-name: cubeSpin;
            animation-name: cubeSpin;
    -webkit-animation-duration: var(--speed);
            animation-duration: var(--speed);
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-delay: var(--delay);
            animation-delay: var(--delay);
  }

  .cube .side {
    display: flex;
    align-content: stretch;
    justify-content: stretch;
    position: absolute;
    width: var(--cube-size);
    height: var(--cube-size);
    -webkit-backface-visibility: visible;
            backface-visibility: visible;
    border: 1px solid var(--fg-color);
    border-radius: 100%;
    /*background: var(--bg-color);*/
  }

  .cube .side.front {
    transform: rotateY(0deg) translateZ(calc(var(--cube-size) / 2));
  }

  .cube .side.back {
    transform: rotateY(180deg) translateZ(calc(var(--cube-size) / 2));
  }

  .cube .side.right {
    transform: rotateY(90deg) translateZ(calc(var(--cube-size) / 2));
  }

  .cube .side.left {
    transform: rotateY(-90deg) translateZ(calc(var(--cube-size) / 2));
  }

  .cube .side.top {
    transform: rotateX(90deg) translateZ(calc(var(--cube-size) / 2));
  }

  .cube .side.bottom {
    transform: rotateX(-90deg) translateZ(calc(var(--cube-size) / 2));
  }</style>

<svelte:window on:resize={render} />
<div bind:this={container} class="container">
  {#each cubes as cube, index}
    <div
      class="cube"
      style="
                    --cube-size: {cube.size}px;
                    --cx: {cube.center.x}px;
                    --cy: {cube.center.y}px;
                    --speed: {(index + 1) * 5000}ms;
                ">
      <div class="side front" />
      <div class="side back" />
      <div class="side left" />
      <div class="side right" />
      <div class="side top" />
      <div class="side bottom" />
    </div>
  {/each}
</div>
